}
.pagination {
  margin: 0 auto;
  padding-top: 0.8em;
  font-size: 0.6em;
}
.pagination>ul {
  width: 100%;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}
.pagination>ul>li {
  list-style-type: none;
}
.pagination>ul>li>.page-link.currentPage {
  background: #007bff;
  color: white;
}
.sitesList .avatar {
  width: 100%;
  height: auto;
}
.list-header-md {
  border: none;
}
.footer-title,
.pagination-box .item {
  background: #f8f8f8;
}

.navBack {
